@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/montserrat-regular.woff2") format("woff2");
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/montserrat-medium.woff2") format("woff2");
}
@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/montserrat-semi-bold.woff2") format("woff2");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/montserrat-semi-bold.woff2") format("woff2");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Montserrat", "Arial", sans-serif;
  color: #011c40;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #f9fbfd;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 103px;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 15px;
  }
}

.advantages {
  position: relative;
  min-height: 562px;
}
@media (max-width: 1023px) {
  .advantages {
    min-height: 588px;
  }
}
@media (max-width: 767px) {
  .advantages {
    min-height: 997px;
  }
}

.advantages__wrapper {
  margin: 0 auto;
  padding: 110px 0;
  max-width: 1160px;
}
@media (max-width: 1023px) {
  .advantages__wrapper {
    padding: 90px 0;
  }
}
@media (max-width: 767px) {
  .advantages__wrapper {
    padding: 57px 0;
  }
}

.advantages__wrapper h2 {
  margin: 0 0 49px 0;
  font-weight: 600;
  font-size: 28px;
  line-height: 42px;
  text-align: center;
}
@media (max-width: 1023px) {
  .advantages__wrapper h2 {
    font-size: 26px;
    line-height: 39px;
  }
}
@media (max-width: 767px) {
  .advantages__wrapper h2 {
    margin: 0 0 30px 0;
    font-size: 24px;
    line-height: 36px;
  }
}

.advantages__wrapper ul {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 40px;
  margin: 0;
  padding: 0;
  list-style-type: none;
}
@media (max-width: 1023px) {
  .advantages__wrapper ul {
    gap: 30px;
  }
}
@media (max-width: 767px) {
  .advantages__wrapper ul {
    grid-template-columns: 1fr;
  }
}

.advantages__wrapper li {
  position: relative;
  padding: 80px 30px 30px 30px;
  height: 100%;
  min-height: 241px;
  background-color: #f9fbfd;
}
@media (max-width: 1023px) {
  .advantages__wrapper li {
    padding: 70px 15px 20px 20px;
    min-width: 206px;
  }
}
@media (max-width: 767px) {
  .advantages__wrapper li {
    min-width: 290px;
  }
}

.advantages__wrapper svg {
  position: absolute;
  top: 30px;
  left: 30px;
}
@media (max-width: 1023px) {
  .advantages__wrapper svg {
    top: 26px;
    left: 20px;
  }
}

@media (max-width: 1023px) {
  .advantages__item--liner svg {
    width: 21px;
    height: 26px;
  }
  .advantages__item--check svg {
    width: 27px;
    height: 26px;
  }
  .advantages__item--boat svg {
    width: 36px;
    height: 26px;
  }
}

.advantages__wrapper h3 {
  margin: 0 0 10px 0;
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
}
@media (max-width: 1023px) {
  .advantages__wrapper h3 {
    line-height: 25.2px;
  }
}
@media (max-width: 767px) {
  .advantages__wrapper h3 {
    margin: 0 0 9px 0;
  }
}

.advantages__wrapper p {
  margin: 0;
  font-size: 14px;
  line-height: 19.6px;
}
@media (max-width: 1023px) {
  .advantages__wrapper p {
    line-height: 20px;
  }
}

@media (min-width: 375px) {
  .advantages__wrapper br {
    display: none;
  }
}

.advantages__image-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #e3ecf6;
  z-index: -1;
}

.advantages img {
  object-fit: cover;
  width: 100%;
  height: 100%;
  opacity: 10%;
}
@media (max-width: 767px) {
  .advantages img {
    object-position: 0;
  }
}

.btn {
  display: inline-flex;
  padding: 10px 20px;
  color: #ffffff;
  background-color: #2c39f2;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.btn--red {
  background-color: #ff1553;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}

.catalog__wrapper {
  margin: 0 auto;
  padding: 85px 0 120px 0;
  max-width: 1130px;
}
@media (max-width: 1023px) {
  .catalog__wrapper {
    padding: 90px 15px 100px 15px;
  }
}
@media (max-width: 767px) {
  .catalog__wrapper {
    padding: 56px 10px 65px 10px;
  }
}

.catalog__wrapper h2 {
  margin: 0 0 49px 0;
  font-weight: 600;
  font-size: 28px;
  line-height: 42px;
  text-align: center;
}
@media (max-width: 1023px) {
  .catalog__wrapper h2 {
    font-size: 26px;
    line-height: 39px;
  }
}
@media (max-width: 767px) {
  .catalog__wrapper h2 {
    margin: 0 0 31px 0;
    font-size: 24px;
    line-height: 36px;
  }
}

.catalog__wrapper ul {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 30px;
  margin: 0;
  padding: 0;
  list-style-type: none;
}
@media (max-width: 1023px) {
  .catalog__wrapper ul {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (max-width: 767px) {
  .catalog__wrapper ul {
    grid-template-columns: 1fr;
  }
}

.catalog__item {
  min-height: 386px;
  color: #f9fbfd;
  overflow: hidden;
  transform-style: preserve-3d;
  perspective: 1200px;
}

.catalog__item-flipper {
  position: relative;
  width: 100%;
  height: 100%;
  transform-style: preserve-3d;
  transition: 0.6s linear;
}

.catalog__item-front,
.catalog__item-back {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
}

.catalog__item-front {
  background-color: #011c40;
  z-index: 2;
  transform: rotateY(0deg);
}
.catalog__item-front::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  display: block;
  width: 100%;
  height: 50%;
  background: linear-gradient(180deg, rgba(1, 28, 64, 0) 0%, #011c40 100%);
}

.catalog__item-back {
  padding: 28px 25px;
  background-color: #011c40;
  overflow-y: auto;
  transform: rotateY(-180deg);
}
@media (max-width: 1023px) {
  .catalog__item-back {
    padding: 28px 35px;
  }
}
@media (max-width: 767px) {
  .catalog__item-back {
    padding: 28px 25px;
  }
}

.catalog__item:hover .catalog__item-flipper,
.catalog__item:focus-within .catalog__item-flipper {
  transform: rotateY(180deg);
}

.catalog__item-front img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.catalog__wrapper h3 {
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
}

.catalog__item-front h3 {
  position: absolute;
  bottom: 0;
  left: 0;
  margin: 0;
  padding: 23px 30px;
  z-index: 3;
}
@media (max-width: 1023px) {
  .catalog__item-front h3 {
    padding: 28px 35px;
  }
}
@media (max-width: 767px) {
  .catalog__item-front h3 {
    padding: 18px 25px;
  }
}

.catalog__item-back ul {
  display: block;
  margin: 0 0 25px 0;
}

.catalog__item-back li {
  margin: 0 0 12px 0;
}

.catalog__item-back h3 {
  margin: 0 0 15px 0;
}

.catalog__item-back p {
  margin: 0;
  font-size: 14px;
  line-height: 19.6px;
}

.catalog__item-back p:first-child {
  margin: 0 0 1px 0;
  font-weight: 500;
}

.catalog__item-back a {
  display: block;
  padding: 9px 0 0 0;
  width: 100%;
  min-height: 40px;
  text-align: center;
  font-weight: 600;
  font-size: 14px;
  line-height: 21px;
  background-color: #f9fbfd;
  color: #011c40;
  user-select: none;
}
@media (hover: hover) {
  .catalog__item-back a:hover:not(.focus-visible) {
    box-shadow: 0 0 10px 1px #f9fbfd;
  }
}
.catalog__item-back a.focus-visible:focus {
  outline: none;
  box-shadow: 0 0 10px 1px #f9fbfd;
}
.catalog__item-back a:active {
  opacity: 94%;
}

.custom-input {
  position: relative;
}
.custom-input.is-invalid input {
  outline: 1px solid #ff1553;
}
.custom-input.is-invalid input:focus, .custom-input.is-invalid input:hover {
  outline: 1.5px solid #ff1553;
}
.custom-input .input-message {
  position: absolute;
  top: -15px;
  left: 0;
}
.custom-input .input-message.is-invalid {
  font-size: 11px;
  line-height: 12px;
  color: #f9fbfd;
}

.form {
  padding: 43px 50px 50px 50px;
  max-width: 403px;
  background-color: #011c40;
}
@media (max-width: 767px) {
  .form {
    padding: 34px 30px 40px 30px;
  }
}

.form p {
  margin: 0 0 28px 0;
  font-weight: 500;
  color: #f9fbfd;
}
@media (max-width: 767px) {
  .form p {
    text-align: center;
    line-height: 25.5px;
  }
}

.form input {
  display: block;
  margin: 0 0 20px 0;
  padding: 11px 16px;
  width: 100%;
  border: none;
  font-size: 14px;
  line-height: 21px;
  background-color: #f9fbfd;
  color: #011c40;
}
@media (hover: hover) {
  .form input:hover:not(.focus-visible) {
    box-shadow: 0 0 10px 1px #f9fbfd;
  }
}
.form input.focus-visible:focus {
  outline: none;
  box-shadow: 0 0 15px 1px #f9fbfd, inset 0 0 0 1.5px #011c40;
}
.form input:disabled {
  opacity: 50%;
}

.form input::-moz-placeholder {
  color: rgba(1, 28, 64, 0.7);
}

.form input::-webkit-input-placeholder {
  color: rgba(1, 28, 64, 0.7);
}

.form__checkbox label {
  position: relative;
  display: flex;
  padding: 0 0 0 23px;
  min-height: 20px;
}
@media (max-width: 767px) {
  .form__checkbox label {
    padding: 0 0 0 28px;
  }
}

.form__control-label {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  font-size: 10px;
  line-height: 12px;
  color: #f9fbfd;
  cursor: pointer;
}

.form__control-mark {
  position: absolute;
  top: 0;
  left: 0;
  width: 18px;
  height: 18px;
  background-color: #f9fbfd;
}
@media (max-width: 767px) {
  .form__control-mark {
    width: 20px;
    height: 20px;
  }
}

.form__checkbox input {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.form__checkbox input[type=checkbox]:checked + .form__control-mark::before {
  content: "";
  position: absolute;
  top: 5px;
  left: 3px;
  width: 12px;
  height: 9px;
  background-image: url("../img/svg/checked.svg");
  background-repeat: no-repeat;
}
@media (max-width: 767px) {
  .form__checkbox input[type=checkbox]:checked + .form__control-mark::before {
    top: 6px;
    left: 4px;
  }
}

.form__checkbox label:hover .form__control-mark {
  box-shadow: 0 0 10px 1px #f9fbfd;
}

.form__checkbox input:focus-visible + .form__control-mark {
  box-shadow: 0 0 15px 1px #f9fbfd, inset 0 0 0 1.5px #011c40;
}

.form button {
  display: block;
  margin: 28px 0 0 0;
  padding: 10px 15px 11px 15px;
  width: 100%;
  min-height: 43px;
  font-weight: 600;
  font-size: 14px;
  line-height: 21px;
  color: #011c40;
  background-color: #f9fbfd;
  border: none;
  user-select: none;
  cursor: pointer;
}
@media (max-width: 767px) {
  .form button {
    margin: 30px 0 0 0;
  }
}
@media (hover: hover) {
  .form button:hover:not(.focus-visible) {
    box-shadow: 0 0 10px 1px #f9fbfd;
  }
}
.form button.focus-visible:focus {
  outline: none;
  box-shadow: 0 0 10px 1px #f9fbfd;
}
.form button:active {
  opacity: 94%;
}
.form button:disabled {
  opacity: 50%;
}

.intro {
  position: relative;
  min-height: 650px;
  color: #f9fbfd;
}
@media (max-width: 1023px) {
  .intro {
    min-height: 638px;
  }
}
@media (max-width: 767px) {
  .intro {
    min-height: 500px;
  }
}

.intro__wrapper {
  margin: 0 auto;
  padding: 270px 95px 50px 95px;
  max-width: 1160px;
}
@media (max-width: 1023px) {
  .intro__wrapper {
    padding: 268px 0 50px 0;
  }
}
@media (max-width: 767px) {
  .intro__wrapper {
    padding: 176px 0 50px 0;
  }
}

.intro__wrapper h1 {
  margin: 0 0 12px 0;
  font-weight: 700;
  font-size: 54px;
  line-height: 66px;
  letter-spacing: 0.7px;
}
@media (max-width: 1023px) {
  .intro__wrapper h1 {
    font-size: 50px;
    line-height: 61px;
  }
}
@media (max-width: 767px) {
  .intro__wrapper h1 {
    margin: 0 0 11px 0;
    font-size: 36px;
    line-height: 45px;
    letter-spacing: 0.5px;
  }
}

.intro__wrapper p {
  margin: 0;
  font-size: 20px;
  line-height: 24.3px;
}
@media (max-width: 1023px) {
  .intro__wrapper p {
    font-size: 18px;
    line-height: 22px;
  }
}
@media (max-width: 767px) {
  .intro__wrapper p {
    font-size: 16px;
    line-height: 22px;
  }
}

@media (min-width: 375px) {
  .intro__wrapper br {
    display: none;
  }
}

.intro__image-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #011c40;
  z-index: -1;
}

.intro img {
  object-fit: cover;
  width: 100%;
  height: 100%;
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
}
@media (max-width: 1023px) {
  .intro img {
    box-shadow: none;
  }
}
@media (max-width: 767px) {
  .intro img {
    object-position: 45.5% 50%;
  }
}

.reservation {
  position: relative;
  min-height: 555px;
}

.reservation__wrapper {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 30px;
  align-items: center;
  margin: 0 auto;
  padding: 50px 60px;
  max-width: 1160px;
}
@media (max-width: 1023px) {
  .reservation__wrapper {
    grid-template-columns: 1fr;
    gap: 53px;
    padding: 88px 0 100px 0;
  }
}
@media (max-width: 767px) {
  .reservation__wrapper {
    gap: 32px;
    padding: 59px 0 65px 0;
  }
}

@media (max-width: 1023px) {
  .reservation__content-wrapper {
    text-align: center;
  }
}

.reservation__wrapper h2 {
  margin: 0 0 15px 0;
  font-weight: 700;
  font-size: 40px;
  line-height: 44px;
  letter-spacing: 0.25px;
}
@media (max-width: 1023px) {
  .reservation__wrapper h2 {
    margin: 0 0 4px 0;
    font-size: 34px;
    line-height: 51px;
    letter-spacing: 0.2px;
  }
}
@media (max-width: 767px) {
  .reservation__wrapper h2 {
    margin: 0 0 14px 0;
    font-size: 24px;
    line-height: 31px;
  }
}

.reservation__wrapper p {
  font-size: 18px;
  line-height: 27px;
}

.reservation__content-wrapper p {
  margin: 0;
}
@media (max-width: 767px) {
  .reservation__content-wrapper p {
    font-size: 16px;
    line-height: 22.4px;
  }
}

@media (max-width: 767px) {
  .reservation__content-wrapper br {
    display: none;
  }
}

.reservation__form {
  justify-self: end;
}
@media (max-width: 1023px) {
  .reservation__form {
    justify-self: center;
    margin: 0 0 0 1px;
  }
}
@media (max-width: 767px) {
  .reservation__form {
    margin: 0;
  }
}

.reservation img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -1;
}

.travelling__wrapper {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 30px;
  margin: 0 auto;
  padding: 120px 0 145px 0;
  max-width: 1160px;
}
@media (max-width: 1023px) {
  .travelling__wrapper {
    padding: 91px 0 93px 0;
  }
}
@media (max-width: 767px) {
  .travelling__wrapper {
    padding: 57px 0 89px 0;
    grid-template-columns: 1fr;
    gap: 30px;
  }
}

.travelling__content-wrapper {
  padding: 56px 0 10px 0;
  max-width: 512px;
}
@media (max-width: 1023px) {
  .travelling__content-wrapper {
    padding: 0;
  }
}
@media (max-width: 767px) {
  .travelling__content-wrapper {
    max-width: 737px;
    padding: 0 0 10px 0;
  }
}

.travelling__content-wrapper h2 {
  margin: 0 0 23px 0;
  font-weight: 600;
  font-size: 28px;
  line-height: 42px;
}
@media (max-width: 1023px) {
  .travelling__content-wrapper h2 {
    margin: 0 0 15px 0;
    font-size: 26px;
    line-height: 36.4px;
  }
}
@media (max-width: 767px) {
  .travelling__content-wrapper h2 {
    font-size: 24px;
    line-height: 33.6px;
  }
}

.travelling__content-wrapper p {
  margin: 0;
}

.travelling__image-wrapper {
  margin: 0 0 0 5px;
}
@media (max-width: 1023px) {
  .travelling__image-wrapper {
    margin: -1px 0 0 0;
  }
}
@media (max-width: 767px) {
  .travelling__image-wrapper {
    margin: 0;
  }
}

.travelling__image-wrapper img {
  object-fit: contain;
  width: 100%;
  height: 100%;
}
@media (max-width: 1023px) {
  .travelling__image-wrapper img {
    object-position: 50% 10px;
  }
}
@media (max-width: 767px) {
  .travelling__image-wrapper img {
    object-position: 50% 4px;
  }
}

